import React, { useState } from "react";
import { useDispatch } from "react-redux";
import { Link, useNavigate } from "react-router-dom";
import Swal from "sweetalert2";
import instance from "../baseUrl/baseUrl";
import { signin } from "../feature/user";

function SignIn() {
  const [email, setemail] = useState("");
  const [password, setPassword] = useState("");
  const dispatch = useDispatch();

  /*============= Toast Fire Notifaction==========*/

  const Toast = Swal.mixin({
    toast: true,
    position: "top-end",
    showConfirmButton: false,
    timer: 3000,
    timerProgressBar: true,
    didOpen: (toast) => {
      toast.addEventListener("mouseenter", Swal.stopTimer);
      toast.addEventListener("mouseleave", Swal.resumeTimer);
    },
  });

  /*================ERROR MESSAGE============= */

  let errorsObj = {
    email: "",
    password: "",
  };

  const [errors, setErrors] = useState(errorsObj);

  function onLogin(e) {
    e.preventDefault();
    let error = false;
    errorsObj = { ...errorsObj };

    if (email === "") {
      errorsObj.email = "*Email address is required";
    } else if (!/\S+@\S+\.\S+/.test(email)) {
      errorsObj.email = "*Email address is invalid";
    }

    if (password === "") {
      errorsObj.password = "*phoneNumber is required";
      error = true;
    }

    setErrors(errorsObj);

    if (error) return;
  }

  /*================SignIn API===============*/
  const navigate = useNavigate();

  const getItem = JSON.parse(localStorage.getItem("user"));
  const SignIn = async () => {
    try {
      const result = await instance.post("/signin", {
        // countryCode:user.countryCode,
        emailOrMobile: email,
        password,
      });
      if (result.data.success) {
        console.log(result);
        Toast.fire({
          icon: "success",
          title: result.data.message,
        });

        localStorage.setItem(
          "user",
          JSON.stringify({
            _id: result.data.data._id,
            active: result.data.data.active,
            username: result.data.data.username,
            email: result.data.data.email,
            countryCode: result.data.data.countryCode,
            phoneNumber: result.data.data.phoneNumber,
            refCode: result.data.data.inviteCode,
          })
        );

        dispatch(
          signin({
            _id: result.data.data._id,
            active: result.data.data.active,
            username: result.data.data.username,
            email: result.data.data.email,
            countryCode: result.data.data.countryCode,
            phoneNumber: result.data.data.phoneNumber,
            refCode: result.data.data.inviteCode,
          })
        );

        navigate("/mine");
      } else {
        Toast.fire({
          icon: "error",
          title: result.data.message,
        });
      }
    } catch (err) {
      console.log("err" + err);
    }
  };

  return (
    <div className="logIn signin-bg">
      <section className="login-form signup-form">
        <div className="container">
          <div className="d-flex justify-content-between align-items-center header-md">
            <div>
              <Link to="/">
                {" "}
                <img
                  src="../../img/logo/logo.png"
                  alt=""
                  className="header-logo img-fluid"
                />
              </Link>
            </div>
          </div>
          <div className="row justify-content-center">
            <div className="col-lg-6 col-md-8 col-sm-10">
              <div className="login-form-bg">
                <h2 className="heading text-center"> Sign In </h2>
                <form autoComplete="off" onSubmit={onLogin}>
                  <input
                    type="email"
                    name="email"
                    value={email}
                    onChange={(e) => setemail(e.target.value)}
                    placeholder="Email Address "
                    className="form-control email-id"
                    required
                  />
                  {errors.email && (
                    <div className="errorMsg">{errors.email}</div>
                  )}

                  <input
                    type="password"
                    name="password"
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                    placeholder="Password"
                    className="form-control pwd"
                  />
                  {errors.password && (
                    <div className="errorMsg">{errors.password}</div>
                  )}

                  <Link
                    to="/forgotPassword"
                    className="text-end w-100 d-inline-block forgot-password"
                  >
                    Forgot Password?
                  </Link>
                  <button type="submit" className="sign-in" onClick={SignIn}>
                    Sign In
                  </button>
                  <p className="text-center">
                    <Link to="/SigninPno">Sign In with Phone Number </Link>
                  </p>

                  <p className="text-center ">
                    Don't have an account?
                    <Link to="/signup"> Sign Up </Link>
                  </p>
                </form>
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
}

export default SignIn;
